            </div>
            <div class="screenshot">
                <p>Problem Screenshot</p>
                <div class="photoWrap">
                    <div class="photo">
                        <img src="../common/images/ku.png">
                        <span class="iconfont red"> &#xe65e; </span>
                    </div>
                    <div class="photo">
                        <img src="../common/images/ku.png">
                        <span class="iconfont red"> &#xe65e; </span>
                    </div>
                    <div class="photo">
                        <img src="../common/images/ku.png">
                        <span class="iconfont red"> &#xe65e; </span>
                    </div>
                    <div class="addPhoto">
                        <img style="display: none;" src="../common/images/ku.png">
                        <img src="../common/images/photo.png">
                    </div>
                </div>
            </div>
        </li>
    </ul>
    <div class="email">
        <van-field v-model="value" placeholder="Email" />
    </div>
    <van-button type="danger">Submit</van-button>
  </div>
</template>

<script>


export default {
  name: 'feedBack',
  data () {
    return {
     value:'',
    
    }
  },
  computed: {
  },
  mounted() {
    this.init();
  },
  methods: {
    init(){

    },
    changeSort(val){
      this.sortValue=val;
      this.sortVisible=false;
    },
    goBack(){
      this.$router.back(-1)
    }


  }
}
</script>

<style lang="scss" scoped>
    ul{
        padding-top: 10px;
        background-color: #F6F6F6;
    }
    li{
        margin-bottom: 10px;
        padding-left: 4%;
        background-color: white;
    }
    .starWrap{
        width: 100%;
        border-bottom: 1px solid #F6F6F6;
        padding-right: 4%;
        font-size: 14px;
        padding-top: 15px;
    }
    .comment{
      width: 100%;
      padding: 0 0px 10px 0px;
      height: 90px;
    }
    .setmeal{
        width: 91px;
        height: 27px;
        font-size: 14px;
        line-height: 25px;
        border: #666666 1px solid;
        border-radius: 2px;
        text-align: center;
        color: #666666;
        float: left;
        margin-top: 10px;
        margin-right: 28px;
    }
    .red{
      border: #E33946 1px solid;
      color: #E33946;
    }
    .leavemessage{
      width: 100%;
      padding-right: 4%;
      font-size: 14px;
    }
    .leaving{
      resize: none;
      border: none;
      margin-top: 10px;
      padding: 5px 10px 0 10px;
      width: 100%;
      background: rgba(246,246,246,1);
    }
    .van-button{
      width: 92%;
      margin: 0 4%;
      margin-top: 25px;
    }
    .screenshot{
        padding-right: 4%;
        padding-top: 15px;
        p{
            font-size: 14px;
        }
        .photoWrap{
            width: 100%;
            display: -webkit-inline-box;
            .addPhoto{
                width: 51px;
                height: 51px;
                margin-top: 10px;
                margin-right: 8%;
                img{
                    height: 100%;
                    width: 100%;
                }
            }
            .photo{
                width: 51px;
                height: 51px;
                margin-top: 10px;
                margin-right: 8%;
                position: relative;
                span{
                    position: absolute;
                    right: -10px;
                    top: -10px;
                    border: none;
                }
                img{
                    height: 100%;
                    width: 100%;
                }
            }
        }
    }
    .email{
        width: 100%;
        border-top: #F6F6F6 10px solid;
        padding: 5px 4% 5px 0;
    }
</style>



